import React, { useState, useEffect } from 'react';
import { EditorState, Modifier, RichUtils, ContentState } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import './App.css';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { convertToHTML } from 'draft-convert';

function App() {
  const [editorState, setEditorState] = useState(
    () => EditorState.createEmpty(),
  );
  const styleMap = {
    RED: {
      color: 'red',
    },
  };

  const handleBeforeInput = (chars, editorState) => {
    const selection = editorState?.getSelection();
    const currentContent = editorState?.getCurrentContent();

    if (!selection || !currentContent) {
      return 'not-handled';
    }

    const blockKey = selection.getStartKey();
    const blockText = currentContent.getBlockForKey(blockKey).getText();

    const prefixStyles = [
      { prefix: '#', length: 1, style: 'header-one', type: 'block' },
      { prefix: '***', length: 3, style: 'UNDERLINE', type: 'inline' },
      { prefix: '**', length: 2, style: 'RED', type: 'inline' },
      { prefix: '*', length: 1, style: 'BOLD', type: 'inline' },
    ];

    for (const { prefix, length, style, type } of prefixStyles) {
      if (chars === ' ' && blockText.startsWith(prefix)) {
        const newContent = Modifier.replaceText(
          currentContent,
          selection.merge({ anchorOffset: 0, focusOffset: length }),
          '' // Remove prefix
        );

        let newEditorState = EditorState.push(
          editorState,
          newContent,
          'remove-range'
        );

        // Remove all inline styles
        const currentStyle = editorState.getCurrentInlineStyle();
        currentStyle.forEach((inlineStyle) => {
          newEditorState = RichUtils.toggleInlineStyle(newEditorState, inlineStyle);
        });
        if (type === 'block') {
          setEditorState(RichUtils.toggleBlockType(newEditorState, style));
        } else {
          setEditorState(RichUtils.toggleInlineStyle(newEditorState, style));
        }

        return 'handled';
      }
    }

    return 'not-handled';
  };

  const handleChange = (newState) => {
    setEditorState(newState);
  };
  useEffect(() => {
    const storedText = localStorage.getItem("editorVal");
    if (storedText) {
      const contentState = ContentState.createFromText(storedText.replace(/"/g, ''));
      const editorState = EditorState.createWithContent(contentState);
      setEditorState(editorState);
    }
  }, []);

  const saveEditorText = () => {
    if (editorState) {
      const contentState = editorState?.getCurrentContent();
      const plainText = contentState?.getPlainText();
      localStorage.setItem("editorVal", JSON.stringify(plainText));
    }

  }

  return (
    <div className="App">
      <header className="App-header">
        Demo Editor by Vijay Kankhedia
      </header>
      <div> <button className="btn-class" onClick={saveEditorText}>Save</button></div>
      <Editor
        editorState={editorState}
        onEditorStateChange={handleChange}
        wrapperClassName="wrapper-class"
        editorClassName="editor-class"
        toolbarHidden
        toolbarClassName="toolbar-class"
        handleBeforeInput={handleBeforeInput}
        customStyleMap={styleMap}
        mention={{
          separator: ' ',
          trigger: '@',
          suggestions: [
            { text: 'JavaScript', value: 'javascript', url: 'js' },
            { text: 'Golang', value: 'golang', url: 'go' },
          ],
        }}
      />
    </div>
  )
}

export default App;
